app.config.globalProperties.unit2 = 6
app.config.globalProperties.appOptions = [
                {name: "Metros", unit: "m"},
                {name: "Kilos", unit: "kg"},
                {name: "Gramos", unit: "g"},
                {name: "Megabytes", unit: "Mb"},
                {name: "Unidades", unit: "U"},
                {name: "Precio", unit: "$"},
                {name: "Euros", unit: "‚Ç¨"},
                ]

app.use(router).mount('#app')